  .container {
    background-color: #F8F4E8; /* Ïª®ÌÖåÏù¥ÎÑà Î∞∞Í≤ΩÏÉâ */
    display: flex;
    justify-content: center; /* Ïò§Î•∏Ï™Ω Ï†ïÎ†¨ */
    align-items: center;
    padding: 20px;
    min-height:100vh;
  }
.wrap_box {
  margin-top: 1000px;
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* Ìïú Ï§ÑÏóê 3Í∞úÏî© */
  gap: 180px;
  justify-content: center; /* Í∞ÄÎ°ú Ï†ïÎ†¨ Ï§ëÏïô */
  margin: 40px auto; /* Ï§ëÏïô Ï†ïÎ†¨ */
  width: fit-content; /* ÎÇ¥Ïö©Ïóê ÎßûÍ≤å ÎÑàÎπÑ Ï°∞Ï†ï */
}


  .box {
  background-color: #FFD46C;
  border-radius: 50%;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 200px;   /* ÎÑàÎπÑÏôÄ ÎÜíÏù¥Î•º Í∞ôÍ≤å Ìï¥ÏÑú Ï†ïÏõêÌòïÏúºÎ°ú */
  height: 130px;
  padding: 0;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}



  .box:nth-child(odd) {
    transform: translateY(20px); /* ÌôÄÏàò Î∞ïÏä§Î•º ÏïÑÎûòÎ°ú Ïù¥Îèô */
  }
  
  .box:nth-child(even) {
    transform: translateY(-20px); /* ÏßùÏàò Î∞ïÏä§Î•º ÏúÑÎ°ú Ïù¥Îèô */
  }
  
  .box:hover {
    transform: scale(1.05); /* hover Ïãú ÏÇ¥Ïßù Ïª§Ïßê */
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.15); /* hover Ïãú Ìö®Í≥º */
  }
  
  .box a {
    text-decoration: none;
    color: #000;
    display: flex;
    flex-direction: column; /* ÏÑ∏Î°ú Î∞∞Ïπò */
    justify-content: center; /* ÏÑ∏Î°ú Ï§ëÏïô Ï†ïÎ†¨ */
    align-items: center; /* Í∞ÄÎ°ú Ï§ëÏïô Ï†ïÎ†¨ */
    height: 100%;
    width: 100%;
  }
  
  .icon img {
    max-width: 44px; /* Í∏∞Î≥∏ ÏïÑÏù¥ÏΩò ÌÅ¨Í∏∞ */
    max-height: 44px;
    position: absolute;
    top: 20%;
    left: 39%;
  }
  

  
  .box p {
    margin: 5px 0 0;
    font-size: 18px;
    text-align: center; 
  }
  
  .arrow {
    position: absolute; 
    bottom: 10px; 
    right: 10px; 
    font-size: 14px; 
    color: #555; 
    background-color: #FBF7EB; 
    border-radius: 50%; 
    width: 24px; 
    height: 24px; 
    display: flex; 
    justify-content: center;
    align-items: center;
  }
  
